/**
 * Augment les objets charges avec deux clefs :
 * - le montant global  payer en fonction de la base de calcul
 * - les "tranches actives" : le dtail du montant par tranche
 */
angular.module('calculator').service('chargesTranchesCalculatorService',['chargesConfig2016', function(chargesConfig2016){

  const service = {};

  /**
   * Retourne le montant pour une tranche de charge.
   *
   * Une tranche est un objet qui peut contenir les clefs suivantes :
   * - montant : peut tre dj rempli pour les montants forfaitaires
   * - taux : le pourcentage  appliquer sur le montant
   * - montant_forfaitaire : si la tranche est un montant fixe en fonction du plafond.
   */
  service.calculerMontantTranche = (tranche, baseCalcul) => {

    var montant = 0;

    // si un montant forfaitaire est prdfini pour cette tranche
    if (tranche.montant_forfaitaire) {
      montant = tranche.montant_forfaitaire;
    }
    // sinon on calcule le montant de la tranche en fonction du taux indiqu
    else if (tranche.taux) {
      montant = baseCalcul * (tranche.taux / 100);
    }

    // on ajoute ou met  jour le montant  notre objet tranche
    tranche.montant = montant.toFixedNumber(2);
    tranche.baseCalcul = baseCalcul;
    return montant;
  };

  /**
   * Calcul la tranche qui correspond  baseDeCalcul en fonction du tableau "tranches".
   * Pour les tranches exclusives, seule UNE tranche est conserv pour le calcul, les
   * tranches prcdentes ou suivantes n'entrent donc en rien dans le calcul du montant
   * de la cotisation
   *
   * @param baseCalcul float | int :
   * @param charge array : tableau d'objet "charge"
   */
  service.calculerTrancheExclusive = (baseCalcul, charge) => {

    charge.montant = 0;

    // on recherche la tranche qui correspond  notre baseCalcul
    var trancheActive = null;

    charge.tranches.forEach(function(tranche) {
      // tant que la base de calcul n'est pas suprieur au plafond en cours, on continue
      // d'itrer.
      if (!trancheActive && baseCalcul <= tranche.plafond) {
        // on a dpass le plafond, on arrte de mettre  jour la variable trancheActive
        // qui contient maintenant notre rponse
        //tranche.baseCalcul = baseCalcul;
        trancheActive = tranche;
      }
    });

    if (trancheActive) {
      charge.montant = service.calculerMontantTranche(trancheActive, baseCalcul).toFixedNumber(2);
      charge.tranchesActives= [trancheActive];
    }

    return charge;
  };

  /**
   * Calcul des charges  tranches cumulatives, tels que l'impot sur les bnfices :
   * - 15% pour pour les 38120 premiers euros, puis 33,33% sur le reste des bnfices
   *
   * @param baseCalcul float | int :
   * @param charge array : tableau d'objet "charges"
   */
  service.calculerTranchesCumulatives = (baseCalcul, charge) => {

    charge.montant = 0;

    // contiendra la liste des tranches qui seront applique
    //  notre base de calcul
    var tranches = [];

    // montant total, toute tranches cumules
    var montant = 0;
    var plancher = 0;

    charge.tranches.forEach((tranche, index) => {

      // on calcule le "planger" de la tranche, qui est soit gal
      // au plafond prcdent, soit  zro si c'est la premire tranche.
      if (typeof tranches[index - 1] !== 'undefined') {
        plancher = tranches[index - 1].plafond;
      }
      // on calcule la diffrence entre le plafond et le plancher
      tranche.intervalle = tranche.plafond - plancher;

      // si la somme est suprieure ou gale au plafond de la tranche courante ...
      if (baseCalcul >= tranche.plafond)
      {
        // ... on calcule le montant d pour la tranche courante
        tranche.baseCalcul = tranche.intervalle;
        tranche.montant = service.calculerMontantTranche(tranche, tranche.baseCalcul).toFixedNumber(2);
        // on ajoute le montant de la cotisation de cette tranche au total.
        montant += tranche.montant;
        // ajout  la liste des tranches qui s'applique  notre cas.
        tranches.push(tranche);
      }

      // mais si la somme est infrieure au plafond courant, c'est que nous sommes  la dernire tranche
      else
      {
        // on calcule le montant pour cette derniere tranche
        var depassement_plancher = baseCalcul - plancher;
        if (depassement_plancher > 0)
        {
          tranche.baseCalcul = depassement_plancher;
          montant += tranche.montant = service.calculerMontantTranche(tranche, tranche.baseCalcul).toFixedNumber(2);
          // ajout  la liste des tranches qui s'appliquent  notre cas.
          tranches.push(tranche);
        }
      }

    });

    charge.montant = montant;
    charge.tranchesActives= tranches;

    return charge;
  };

  return service;

}]);




